                  <!-- 왼쪽 텍스트 -->
                  <div class="text-box">
                    <div class="tag-box">
                      <span v-for="item2 in item.spanT" :key="item2">{{item2}}</span>
                    </div>
                    <h2>{{item.h2T}}</h2>
                    <p>{{item.pT}}</p>
                  </div>
                  <!-- 오른쪽 이미지 -->
                  <div class="img-box">
                    <a href="#"><img :src="item.img" alt=""></a>
