              <div class="left-btn-box">
                <!-- 좌 인덱스박스 -->
                <div class="index-wrap">
                  <span class="current-number">1</span>
                  <i>/</i>
                  <span class="total-number">12</span>
                </div>
                <!-- 우 클릭버튼박스 -->
                <div class="control-wrap">
                  <!-- 왼쪽화살표 -->
                  <button class="left-arrow-btn">
                    <li class="left-arrow"></li>
                  </button>
                  <!-- 정지플레이버튼 -->
                  <button class="play-stop-btn">
                    <li class="paly-stop play"></li>
                  </button>
                  <!-- 오른쪽화살표 -->
                  <button class="right-arrow-btn">
                    <li class="right-arrow"></li>
                  </button>
                </div>
              </div>
            </div>

            <div class="nanum"></div>
            <!-- 슬라이드선택버튼 -->
            <div class="page-btn-box">
              <div class="page-container">
                <span :class="['page-btn','page'+(idx+1), idx===0?'on':'']" v-for="name,idx in btnname" :key="name">{{name}}</span>
              </div>
            </div>
            <!-- 하단펼쳐지는 메뉴 버튼 -->
            <div class="right-btn-box">
              <div :class="['right-btn', {'on':isOn}]" @click.prevent="onClickSubBtn">
                <li :class="['under', {'on':isOn}]"></li>
              </div>
            </div>
          </div>
        </div>
        <!-- 하단펼침섹션 displaynone-->
        <div class="under-btn-box" v-show="isOn">
          <div class="title">
            <h2>진행중인 모든 이벤트</h2>
            <button class="under-btn-close" @click.prevent="onClickCloseBtn">닫기</button>
          </div>
          <div class="container">
            <span :class="['under-page-btn','page'+(idx+1), idx===0?'on':'']" v-for="name,idx in btnname" :key="name">{{name}}</span>
          </div>
        </div>
      </section>
</template>

