</template>

<script>
import $ from 'jquery';
export default {
    name:'Section4Component',
    props:[],
    data(){
      return{
        h4Title:'왕초보도 할 수 있어요 💪',
        pText:'이미 검증된 쉽고 친절한 입문 강의!!',
        s4slide:[
          {img:'./images/s4slide1.jpg',title:'[C#과 유니티로 만드는 MMORPG 게임 개발 시리즈] Part1: C# 기초 프로그래밍 입문',instructor:'Rookiss',rating:'★★★★★',price:'₩44,000',tag:'+5800명'},
          {img:'./images/s4slide2.jpg',title:'[게임 프로그래머 입문 올인원] C++ & 자료구조/알고리즘 & STL & 게임 수학 & Windows API & 게임 서버',instructor:'Rookiss',rating:'★★★★★',price:'₩396,000',tag:'+900명'},
          {img:'./images/s4slide3.jpg',title:'[코드캠프] 부트캠프에서 만든 고농축 프론트엔드 코스',instructor:'코드캠프',rating:'★★★★☆',price:'₩297,000',tag:'+1,600명'},
          {img:'./images/s4slide4.jpg',title:'[코드캠프] 부트캠프에서 만든 고농축 프론트엔드 코스',instructor:'코드캠프',rating:'★★★★★',price:'₩297,000',tag:'+1,200명'},
          {img:'./images/s4slide5.jpg',title:'실리콘밸리 엔지니어가 가르치는 파이썬 기초부터 고급까지',instructor:'미쿡엔지니어',rating:'★★★★★',price:'₩71,500',tag:'새강의'},
          {img:'./images/s4slide6.jpg',title:'비전공 기획자 및 관리자를 위한 IT 필수 지식',instructor:'불꽃남자25',rating:'★★★★★',price:'₩93,500',tag:'+1,100명'},
          {img:'./images/s4slide7.jpg',title:'[백문이불여일타] 데이터 분석을 위한 기초 SQL',instructor:'데이터리안',rating:'★★★★★',price:'₩16,500',tag:'+4,600'},
          {img:'./images/s4slide8.jpg',title:'[C++과 언리얼로 만드는 MMORPG 게임 개발 시리즈] Part1: C++ 프로그래밍 입문',instructor:'Rookiss',rating:'★★★★★',price:'₩110,000',tag:'+2,700명'},
          {img:'./images/s4slide9.jpg',title:'제대로 파는 HTML CSS - by 얄코',instructor:'얄팍한 코딩사전',rating:'★★★★★',price:'₩44,000',tag:'+1,900명'},
          {img:'./images/s4slide10.jpg',title:'파이썬입문과 크롤링기초 부트캠프 [파이썬, 웹, 데이터 이해 기본까지] (업데이트)',instructor:'잔재미코딩 DaveLee',rating:'★★★★★',price:'₩59,400',tag:'+5,600명'},
          {img:'./images/s4slide11.jpg',title:'웹기획자의 커리어 확장 로드맵',instructor:'웹피소드',rating:'☆☆☆☆☆',price:'₩18,700',tag:'새강의'},
          {img:'./images/s4slide12.jpg',title:'IT 회사에서 비개발자가 살아남기 위한 모든 개발 지식 A to Z',instructor:'그랩',rating:'★★★★☆',price:'₩55,000',tag:'+1,500명'},
          {img:'./images/s4slide13.jpg',title:'데이터 분석을 위한 파이썬: 입문부터 꿀팁까지',instructor:'데이터리안',rating:'★★★★★',price:'₩38,500',tag:'+400명'},
          {img:'./images/s4slide14.jpg',title:'친절한 블렌더 - [LV.0] 입문 필수',instructor:'codemod',rating:'★★★★☆',price:'₩77,000',tag:'+200명'},
          {img:'./images/s4slide15.jpg',title:'게임 모델링을 위한 3DsMAX 입문과 기초활용',instructor:'청강문화산업대학교',rating:'★★★★★',price:'₩77,000',tag:'없음'},
        ]
      }
    },
    mounted() {

      let s4cnt = 0;

      function s4slide(){
        $('.s4-slid-wrap').stop().animate({left: `${-1200 * s4cnt}px` },600)

        if (s4cnt>1){
          $('.s4right-btn').animate({opacity: '0.2'});
        }
        if (s4cnt < 1){
          $('.s4left-btn').animate({opacity: '0.2'});
        }
      }

      $('.s4right-btn').on({
        click(){
          if (s4cnt>1){
            s4cnt = 1;
          }
          s4nextCount();
        }
      })
      $('.s4left-btn').on({
        click(){
          console.log(s4cnt)
          if(s4cnt>1){
            s4cnt = 2;
          }
          if (s4cnt < 1){
            s4cnt = 1;
          }
          s4prevCount();
          $('.s4right-btn').animate({opacity: '1'});
        }
      })



      //다음 카운트
      function s4nextCount() {
        s4cnt++;
        s4slide();
      }

      //이전 카운트
      function s4prevCount() {
        s4cnt --;    
        s4slide();    
      }



    },
}
</script>

